<template>
    <p-data-table
        :value="completeList"
        v-model:selection="selectedList"
        selection
        dataKey="cycleId"
        class="p-datatable-sm"
        showGridlines
        responsiveLayout="scroll"
        :loading="loading"
        :scrollable="true"
        scrollHeight="calc(100vh - 130px)"
    >
        <template #header>
            <div class="table-header" style="display: flex;">
                <label class="p-mr-2 p-mt-2">Cycle Year</label>
                <p-input-text
                    v-model="cycleYear"
                    class="p-mr-2"
                    :class="{ 'p-invalid': cycleYearRequired }"
                />

                <label class="p-mr-2 p-mt-2">Filter</label>
                <p-multi-select
                    v-model="sourceFilter"
                    :options="sourceFilterOpts"
                    optionLabel="label"
                    optionValue="value"
                    class="p-mr-2"
                    style="width: 200px;"
                />

                <label class="p-mr-2 p-mt-2">Cycle Cancelled</label>
                <p-dropdown
                    v-model="cycleCancelled"
                    :options="cycleCancelledOption"
                    optionLabel="label"
                    optionValue="value"
                    style="width: 80px;"
                    class="p-mr-2"
                />

                <label class="p-mr-2 p-mt-2">Cycle Type</label>
                <p-multi-select
                    v-model="cycleType"
                    :options="cycleTypeOptions"
                    optionLabel="label"
                    optionValue="value"
                    class="p-mr-2"
                    style="width: 200px;"
                />

                <p-button
                    label="Filter"
                    class="p-mr-2 p-button-sm"
                    @click="filter"
                />

                <p-button
                    label="Export XLS"
                    class="p-mr-2 p-button-sm p-button-success"
                    @click="download"
                />
            </div>
        </template>

        <p-column
            selectionMode="multiple"
            headerStyle="width: 3em"
            style="width: 3rem; max-width: 3rem;"
        />
        <p-column field="patientName" header="Patient name" />
        <p-column
            field="patientId"
            header="Chart ID"
            style="max-width: 12rem"
        />
        <p-column
            field="dateCompleted"
            header="Date Completed"
            style="max-width: 12rem"
        />
        <p-column
            field="cycleNo"
            header="Cycle Number"
            style="max-width: 10rem"
        />
        <p-column field="cycleId" header="Cycle id" style="max-width: 7rem" />
        <p-column
            field="cycleName"
            header="Cycle Type"
            style="max-width: 8rem"
        />
        <p-column
            field="cycleNoType"
            header="Cycle no type"
            style="max-width: 8rem"
        />
        <p-column field="ohip" header="Funded" style="max-width: 7rem">
            <template #body="{ data }">
                {{ data.ohip == 1 ? "Yes" : "No" }}
            </template>
        </p-column>
        <p-column
            field="cycleDate"
            header="Cycle Date"
            style="max-width: 120px"
        >
            <template #body="{ data }">
                {{ data.cycleDate.split(" ")[0] }}
            </template>
        </p-column>

        <p-column field="source" header="Source" />

        <p-column></p-column>

        <template #footer>
            <p-paginator
                :rows="20"
                :totalRecords="totalRecords"
                :rowsPerPageOptions="[20, 50, 100]"
                @page="onPage($event)"
            />
        </template>
    </p-data-table>
</template>

<script>
import { ORIGIN } from "@/config";
import {
    getBornUncompleteList,
    getBronCsvMulti,
    markAsUpload
} from "@/service/art-cycle";
import { computed, ref, watch, onMounted } from "vue";
import { useStore } from "vuex";

export default {
    setup() {
        const store = useStore();

        const completeList = ref([]);
        const selectedList = ref([]);
        const loading = ref(false);
        const totalRecords = ref(0);
        const uploaded = ref(false);
        const cycleYear = ref();
        const page = ref(1);
        const perPage = ref(20);
        const sourceFilter = ref([]);
        const sourceFilterOpts = ref([
            { value: "cycle-monitoring", label: "Cycle monitoring" },
            { value: "opu", label: "Opu" },
            { value: "pregnancy", label: "Pregnancy" },
            { value: "fertilization", label: "Fertilization" },
            { value: "embryo-transfer", label: "Embryo transfer" }
        ]);
        const cycleYearRequired = ref(false);

        const cycleCancelled = ref(null);
        const cycleCancelledOption = ref([
            { label: "Yes", value: "yes" },
            { label: "No", value: "no" },
            { label: "All", value: "all" }
        ]);

        const cycleType = ref(null);
        const cycleTypeOptions = computed(
            () => store.state.artCycle.cycleTypeOptions
        );

        const getUncompleteList = async () => {
            if (cycleYear.value == null || cycleYear.value == "") {
                cycleYearRequired.value = true;
                return;
            }
            cycleYearRequired.value = false;
            loading.value = true;
            const { data, total } = await getBornUncompleteList(
                page.value,
                perPage.value,
                cycleYear.value,
                sourceFilter.value,
                cycleCancelled.value,
                cycleType.value.map((item) => item.toUpperCase())
            );
            completeList.value = data;
            totalRecords.value = total;
            loading.value = false;
            selectedList.value = [];
        };

        const onPage = (event) => {
            page.value = event.page + 1;
            perPage.value = event.rows;
            getUncompleteList();
        };

        const cyclePayload = () => {
            return selectedList.value.map((item) => ({
                cycleId: item.cycleId,
                patientId: item.patientId
            }));
        };

        const download = async () => {
            const payload = {
                cycle: cyclePayload()
            };

            const resp = await getBronCsvMulti(payload);
            if (resp.status == "failed") return;

            window.open(`${ORIGIN}/${resp.csv}`);
            selectedList.value = [];
        };

        const markAsUploadHandler = async () => {
            const payload = {
                cycle: cyclePayload(),
                status: 1
            };
            await markAsUpload(payload);
            getUncompleteList();
        };

        return {
            completeList,
            selectedList,
            loading,
            uploaded,
            cycleYear,
            cycleYearRequired,
            totalRecords,
            onPage,
            download,
            markAsUploadHandler,
            filter: () => {
                getUncompleteList();
            },
            sourceFilter,
            sourceFilterOpts,
            cycleCancelled,
            cycleCancelledOption,
            cycleType,
            cycleTypeOptions
        };
    }
};
</script>
